import { UMKM_DATA } from "@/data/umkmData";
import NavDirektori from "@/components/navDirektori";
import FooterDirektori from "@/components/footerDirektori";
import DetailContent from "./DetailContent";
import Link from "next/link";
import { notFound } from "next/navigation";

/* ------------------------------------------------------------------
   GENERATE STATIC PARAMS
   Pre-render semua halaman UMKM untuk optimal performance
------------------------------------------------------------------ */
export async function generateStaticParams() {
  return UMKM_DATA.map((umkm) => ({
    slug: umkm.slug,
  }));
}

/* ------------------------------------------------------------------
   GENERATE METADATA (SEO Optimization)
   Dynamic metadata per UMKM untuk better SEO
------------------------------------------------------------------ */
export async function generateMetadata({ params }) {
  const { slug } = await params;
  const umkm = UMKM_DATA.find((item) => item.slug === slug);

  if (!umkm) {
    return {
      title: "UMKM Tidak Ditemukan | Gelap Nyawang",
    };
  }

  return {
    title: `${umkm.name} - ${umkm.short} | Gelap Nyawang`,
    description: `${umkm.short} ‚Ä¢ Rating ${umkm.rating}/5 ‚Ä¢ ${umkm.rangeHarga} ‚Ä¢ ${umkm.alamat}`,
    openGraph: {
      title: umkm.name,
      description: umkm.short,
      images: [umkm.img],
      type: "website",
    },
  };
}

/* ------------------------------------------------------------------
   HALAMAN DETAIL UMKM (Fixed Version)
------------------------------------------------------------------ */
export default async function DetailUMKM({ params }) {
  const { slug } = await params;
  const umkm = UMKM_DATA.find((item) => item.slug === slug);

  // Next.js 404 page jika tidak ditemukan
  if (!umkm) {
    notFound();
  }

  // ‚úÖ FIX: Gallery fallback yang proper
  const gallery = umkm.gallery && umkm.gallery.length > 0 
    ? umkm.gallery 
    : [umkm.img, umkm.img, umkm.img];

  return (
    <main className="min-h-screen bg-bg-soft dark:bg-bg-base text-text-primary transition-theme">
      <NavDirektori />

      {/* ------------------------------------------------------------
         HERO SECTION (Enhanced)
      ------------------------------------------------------------ */}
      <section className="relative w-full h-[380px] md:h-[480px] overflow-hidden">
        {/* Background Image */}
        <div className="absolute inset-0 pt-16 md:pt-20">
          <img
            src={umkm.img}
            alt={umkm.name}
            className="w-full h-full object-cover brightness-75"
            loading="eager"
            fetchpriority="high"
          />
        </div>

        {/* Gradient Overlays */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent" />
        <div className="absolute inset-0 bg-gradient-to-r from-black/30 to-transparent" />

        {/* Content */}
        <div className="absolute bottom-0 left-0 right-0 px-6 md:px-12 pb-8 md:pb-12">
          <div className="max-w-7xl mx-auto">
            {/* Category Badge */}
            <div className="flex items-center gap-2 mb-3">
              <span className="px-3 py-1 rounded-full bg-primary/90 backdrop-blur-sm 
                               text-white text-xs font-semibold uppercase tracking-wide">
                {umkm.category}
              </span>
              
              {/* Rating Badge */}
              <div className="flex items-center gap-1 px-3 py-1 rounded-full 
                              bg-white/90 backdrop-blur-sm">
                <span className="text-yellow-500">‚≠ê</span>
                <span className="text-sm font-bold text-gray-900">{umkm.rating}</span>
              </div>
            </div>

            {/* Title */}
            <h1 className="text-3xl sm:text-4xl md:text-6xl font-extrabold text-white 
                           drop-shadow-2xl mb-3 leading-tight">
              {umkm.name}
            </h1>

            {/* Short Description */}
            <p className="text-white/95 text-base md:text-xl drop-shadow-lg max-w-2xl 
                          leading-relaxed">
              {umkm.short}
            </p>

            {/* Price Range */}
            <p className="text-white/90 text-lg md:text-xl font-semibold mt-3 drop-shadow">
              üí∞ {umkm.rangeHarga}
            </p>
          </div>
        </div>
      </section>

      {/* ------------------------------------------------------------
         MAIN CONTENT (Pass umkm prop properly)
      ------------------------------------------------------------ */}
      <DetailContent umkm={umkm} gallery={gallery} />

      <FooterDirektori />
    </main>
  );
}